body {
  font-family: Arial;
}

.layout {
  max-width: 960px;
  margin: 0 auto;
}

.fluid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.fluid__image-container {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  margin: 20px auto;
}

.fluid__instructions {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  margin: 0 20px;
}

@media (min-width: 480px) {
  .fluid {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .fluid__image-container{
    -ms-flex: 0 0 30%;
        flex: 0 0 30%;
  }

  .fluid__instructions {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
    padding-top: 20px;
  }
}



